body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.cards-container {
  --rows-counts: 1;

  padding: 50px 40px;
  display: grid;
  grid-template-columns: repeat(var(--rows-counts), 200px);
  gap: 46px 48px;
  justify-content: center;
}

@media (min-width: 488px) {
  .cards-container {
    --rows-counts: 2;
  }
}

@media (min-width: 768px) {
  .cards-container {
    --rows-counts: 3;
  }
}

@media (min-width: 1024px) {
  .cards-container {
    --rows-counts: 4;
  }
}

.card {
  display: flex;
  flex-direction: column;
  width: 200px;
  box-sizing: border-box;
  border: 1px solid $text-color-smoke;

  align-items: center;

  &__image {
    margin-top: 32px;
    width: 160px;
    height: 134px;
  }
  &__product_name {
    margin: 0;
    margin-top: 40px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    text-align: left;
    width: $block-width;
    color: $text-color-black;
  }

  &__code {
    margin: 0;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    text-align: left;
    color: $text-color-grey;
    align-self: flex-start;
    margin-top: 4px;
    margin-left: 16px;
  }
  &__price {
    margin: 0;
    display: flex;
    justify-content: space-between;
    width: $block-width;
    margin-top: 24px;
  }
  &__text {
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    text-align: left;
    color: $text-color-grey;
  }
  &__dollars {
    width: 50px;

    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    text-align: right;
    color: $text-color-black;
  }

  &__button {
    margin-top: 16px;
    margin-bottom: 16px;
    width: $block-width;
    height: 40px;
    border-radius: 5px;
    background-color: $text-color-blue;
    border: 1px solid $text-color-smoke;
    color: $text-color-white;

    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    text-align: center;
  }

  &__button:hover {
    transition: all 300ms;
    background: $text-color-white;
    border: 1px solid $text-color-blue;
    color: $text-color-blue;
  }

  &:hover {
    transition: transform 300ms;
    transform: scale(1.2);
    .card__product_name {
      color: #34568b;
    }
  }
}
